"use client";

import { useEffect, useState } from "react";

type Player = {
  id: number;
  name: string;
  score: number;
  battles: string;
  avatar: string;
};

type MVPCardProps = {
  player: Player;
};

type PodiumSectionProps = {
  leaderboard: Player[];
};

type PodiumBlockProps = {
  place: number;
  user: Player;
  height: string;
  gradient: string;
  border: string;
  scoreColor: string;
  crown?: boolean;
};

// Static fallback if DB call fails or is empty
const fallbackLeaderboard: Player[] = [
  {
    id: 1,
    name: "Andrei Popescu",
    score: 420,
    battles: "69 / 88",
    avatar: "/images/rafa.jpg",
  },
  {
    id: 2,
    name: "Mihai Ionescu",
    score: 415,
    battles: "73 / 100",
    avatar: "/images/salam.jpg",
  },
  {
    id: 3,
    name: "Robert Vadean",
    score: 390,
    battles: "63 / 89",
    avatar: "/images/guta.jpg",
  },
  {
    id: 4,
    name: "Godja Flavius",
    score: 385,
    battles: "59 / 90",
    avatar: "/images/romanes.jpg",
  },
  {
    id: 5,
    name: "Costel Marinescu",
    score: 360,
    battles: "53 / 112",
    avatar: "/images/tzanca.jpg",
  },
  {
    id: 6,
    name: "Daniel Boloaga",
    score: 324,
    battles: "24 / 76",
    avatar: "/images/minune.jpg",
  },
  {
    id: 7,
    name: "Bogdan Georgescu",
    score: 290,
    battles: "32 / 97",
    avatar: "/images/ciorba.jpg",
  },
];

export default function LeaderboardPage() {
  const [players, setPlayers] = useState<Player[]>([]);
  const [loading, setLoading] = useState(true);

  const [search, setSearch] = useState("");
  const [minScore, setMinScore] = useState("");
  const [maxScore, setMaxScore] = useState("");
  const [minBattles, setMinBattles] = useState("");
  const [sortBy, setSortBy] = useState<"score" | "name">("score");

  useEffect(() => {
    const fetchLeaderboard = async () => {
      try {
        const res = await fetch("/api/leaderboard");
        const data = await res.json();

        if (res.ok && Array.isArray(data.leaderboard)) {
          if (data.leaderboard.length >= 3) {
            setPlayers(data.leaderboard);
          } else {
            setPlayers(fallbackLeaderboard);
          }
        } else {
          setPlayers(fallbackLeaderboard);
        }
      } catch (err) {
        console.error("Error fetching leaderboard:", err);
        setPlayers(fallbackLeaderboard);
      } finally {
        setLoading(false);
      }
    };

    fetchLeaderboard();
  }, []);

  const baseLeaderboard = players.length ? players : fallbackLeaderboard;

  const filteredLeaderboard = [...baseLeaderboard]
    .filter((p) => {
      if (search.trim().length > 0) {
        const term = search.toLowerCase();
        if (!p.name.toLowerCase().includes(term)) return false;
      }

      if (minScore !== "" && p.score < Number(minScore)) return false;
      if (maxScore !== "" && p.score > Number(maxScore)) return false;

      if (minBattles !== "") {
        const wins = parseInt(p.battles.split("/")[0].trim(), 10);
        if (wins < Number(minBattles)) return false;
      }

      return true;
    })
    .sort((a, b) => {
      if (sortBy === "score") {
        return b.score - a.score;
      }
      return a.name.localeCompare(b.name);
    });

  const podiumSource = baseLeaderboard.slice(0, 3);

  return (
    <div className="min-h-screen bg-[#0b0c0d] text-white px-6 pt-28 pb-20">
      <style>{globalStyles}</style>

      <div className="max-w-7xl mx-auto grid grid-cols-1 xl:grid-cols-3 gap-10">
        {/* LEFT ‚Äî PODIUM + BOARD */}
        <div className="xl:col-span-2">
          <div className="flex items-center justify-between mb-8">
            <h1 className="text-3xl font-bold">Leaderboard</h1>

            {loading && (
              <span className="text-xs text-gray-400 animate-pulse">
                Loading live data‚Ä¶
              </span>
            )}
          </div>

          {podiumSource.length >= 3 && (
            <PodiumSection leaderboard={podiumSource} />
          )}

          {/* LEADERBOARD LIST */}
          <div className="bg-[#0f172a] border border-[#1e293b] rounded-2xl p-4 shadow-xl">
            <div className="flex flex-col gap-3 md:flex-row md:items-center md:justify-between px-2 pb-4 border-b border-[#1e293b]">
              <div>
                <h2 className="text-lg font-semibold">Monthly Leaderboard</h2>
                <p className="text-xs text-gray-400 mt-1">
                  {filteredLeaderboard.length} Users found ‚Ä¢{" "}
                  {baseLeaderboard.length} total
                </p>
              </div>

              <div className="flex gap-2">
                <button className="px-3 py-1 bg-emerald-600 rounded-lg text-sm">
                  Monthly
                </button>
                <button className="px-3 py-1 bg-[#1e293b] rounded-lg text-sm">
                  All Time
                </button>
              </div>
            </div>

            {/* Filters */}
            <div className="mt-4 mb-3 grid grid-cols-1 md:grid-cols-4 gap-3 text-sm">
              <div className="md:col-span-2">
                <label className="text-gray-400 text-xs uppercase tracking-wide">
                  Search player
                </label>
                <div className="mt-1 flex items-center gap-2 bg-[#0b1220] border border-[#1e293b] rounded-lg px-2 py-1.5">
                  <span className="text-gray-500 text-sm">üîç</span>
                  <input
                    value={search}
                    onChange={(e) => setSearch(e.target.value)}
                    placeholder="Type a name‚Ä¶"
                    className="bg-transparent flex-1 outline-none text-sm text-gray-100 placeholder:text-gray-500"
                  />
                </div>
              </div>

              <div>
                <label className="text-gray-400 text-xs uppercase tracking-wide">
                  Min score
                </label>
                <input
                  type="number"
                  value={minScore}
                  onChange={(e) => setMinScore(e.target.value)}
                  placeholder="300"
                  className="mt-1 w-full bg-[#0b1220] border border-[#1e293b] rounded-lg px-2 py-1.5 outline-none text-sm"
                />
              </div>

              <div>
                <label className="text-gray-400 text-xs uppercase tracking-wide">
                  Min wins
                </label>
                <input
                  type="number"
                  value={minBattles}
                  onChange={(e) => setMinBattles(e.target.value)}
                  placeholder="50"
                  className="mt-1 w-full bg-[#0b1220] border border-[#1e293b] rounded-lg px-2 py-1.5 outline-none text-sm"
                />
              </div>

              <div className="md:col-span-2 flex flex-col md:flex-row gap-3">
                <div className="flex-1">
                  <label className="text-gray-400 text-xs uppercase tracking-wide">
                    Max score
                  </label>
                  <input
                    type="number"
                    value={maxScore}
                    onChange={(e) => setMaxScore(e.target.value)}
                    placeholder="450"
                    className="mt-1 w-full bg-[#0b1220] border border-[#1e293b] rounded-lg px-2 py-1.5 outline-none text-sm"
                  />
                </div>

                <div className="flex-1">
                  <label className="text-gray-400 text-xs uppercase tracking-wide">
                    Sort by
                  </label>
                  <div className="mt-1 flex bg-[#0b1220] border border-[#1e293b] rounded-lg overflow-hidden">
                    <button
                      onClick={() => setSortBy("score")}
                      className={`flex-1 px-2 py-1.5 text-xs font-medium ${
                        sortBy === "score"
                          ? "bg-emerald-600 text-white"
                          : "text-gray-300"
                      }`}
                    >
                      Score
                    </button>
                    <button
                      onClick={() => setSortBy("name")}
                      className={`flex-1 px-2 py-1.5 text-xs font-medium ${
                        sortBy === "name"
                          ? "bg-emerald-600 text-white"
                          : "text-gray-300"
                      }`}
                    >
                      Name
                    </button>
                  </div>
                </div>
              </div>
            </div>

            {/* List */}
            <ul className="mt-2 space-y-3">
              {filteredLeaderboard.map((p) => (
                <li
                  key={p.id}
                  className="flex items-center justify-between bg-[#1e293b] px-4 py-3 rounded-xl hover:bg-[#243045] transition"
                >
                  <div className="flex items-center gap-3">
                    <div className="w-12 h-12 bg-[#0f172a] rounded-full flex items-center justify-center text-2xl overflow-hidden">
                      <img
                        src={p.avatar}
                        alt={p.name}
                        className="w-full h-full object-cover"
                      />
                    </div>
                    <div>
                      <p className="font-medium">{p.name}</p>
                      <p className="text-sm text-emerald-400">
                        Quests completed: {p.battles}
                      </p>
                    </div>
                  </div>

                  <span className="text-lg font-semibold text-emerald-400">
                    üíé{p.score}
                  </span>
                </li>
              ))}

              {filteredLeaderboard.length === 0 && (
                <li className="text-center text-sm text-gray-500 py-6">
                  No players found matching the criteria.
                </li>
              )}
            </ul>
          </div>
        </div>

        {/* RIGHT ‚Äî REWARDS + MVP CARD */}
        <div>
          <h2 className="text-2xl font-bold mb-4 flex items-center gap-2">
            üèÜ Monthly Rewards
          </h2>

          <p className="text-gray-400 mb-6">
            Top 10 receive monthly points rewards based on their position.
          </p>

          <div className="space-y-4">
            {[
              { place: "1st", amount: 250 },
              { place: "2nd", amount: 200 },
              { place: "3rd", amount: 150 },
              { place: "4th", amount: 100 },
              { place: "5th", amount: 75 },
              { place: "6th", amount: 60 },
              { place: "7th", amount: 50 },
              { place: "8th", amount: 40 },
              { place: "9th", amount: 30 },
              { place: "10th", amount: 25 },
            ].map((r, i) => {
              const isTop3 = i < 3;
              return (
                <div
                  key={r.place}
                  className={`
                    flex items-center justify-between px-5 py-4 rounded-2xl border shadow
                    transition relative overflow-hidden
                    ${
                      isTop3
                        ? "bg-[#1e293b] border-[#334155]"
                        : "bg-[#0f172a] border-[#1e293b] hover:bg-[#1c2535]"
                    }
                  `}
                  style={
                    isTop3
                      ? {
                          backgroundImage:
                            "linear-gradient(135deg, rgba(148,163,184,0.2), rgba(30,41,59,0.6))",
                        }
                      : {}
                  }
                >
                  <div className="flex items-baseline gap-1">
                    <span
                      className={`text-lg font-bold ${
                        isTop3 ? "text-white" : "text-gray-200"
                      }`}
                    >
                      {r.place}
                    </span>
                    <span className="text-gray-400 text-sm">place</span>
                  </div>

                  <span
                    className={`text-xl font-extrabold ${
                      isTop3 ? "text-white" : "text-emerald-400"
                    }`}
                  >
                    üíé{r.amount}
                  </span>
                </div>
              );
            })}
          </div>

          {baseLeaderboard.length > 0 && (
            <MVPCard player={baseLeaderboard[0]} />
          )}
        </div>
      </div>
    </div>
  );
}

/* MVP Card, PodiumSection, PodiumBlock + globalStyles stay the same as before */

const globalStyles = `
@keyframes bounceIn {
  0% { opacity: 0; transform: translateY(60px) scale(0.9); }
  60% { opacity: 1; transform: translateY(-10px) scale(1.05); }
  100% { opacity: 1; transform: translateY(0) scale(1); }
}

.animate-bounce-in {
  animation: bounceIn 0.9s forwards ease-out;
}

@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(20px) scale(0.95); }
  100% { opacity: 1; transform: translateY(0) scale(1); }
}

.animate-fade-in {
  animation: fadeIn 0.8s ease forwards;
}

@keyframes pulseBorder {
  0%,100% { box-shadow: 0 0 25px rgba(16,185,129,0.5); }
  50% { box-shadow: 0 0 35px rgba(16,185,129,0.75); }
}

.animate-pulse-border {
  animation: pulseBorder 2s infinite ease-in-out;
}

@keyframes shimmerMove {
  0% { transform: translateX(-150%); }
  100% { transform: translateX(200%); }
}

.animate-shimmer {
  animation: shimmerMove 2.5s infinite;
}

@keyframes growBar {
  0% { width: 0%; }
  100% { width: 78%; }
}

.animate-grow-bar {
  animation: growBar 1.2s ease-out forwards;
}

@keyframes crownBounce {
  0% { transform: translate(-50%, -10px); }
  50% { transform: translate(-50%, -18px); }
  100% { transform: translate(-50%, -10px); }
}

.animate-crown-bounce {
  animation: crownBounce 1.8s infinite ease-in-out;
}
`;
